}

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Roboto';
}

img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
}

header {
    display: flex;
    flex-direction: row;
    padding: 10px 20px;
    justify-content: space-between;
    align-items: center;
}

header nav {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

header .img-container {
    max-width: 20px;
    max-height: 1.15rem;
}

.btn-primary,
.btn-secondary,
.btn-toggle,
.btn-submit {
    background-color: inherit;
    border-style: none;
    font-size: 1.15rem;
}

.btn-primary:hover,
.btn-secondary:hover,
.btn-toggle:hover,
.btn-submit:hover {
    cursor: pointer;
}

.btn-primary .img-container:hover,
.btn-secondary .img-container:hover {
    transform: scale(1.5);
}

.btn-round {
    border-radius: 50%;
}

.btn-toggle {
    padding: 5px 10px;
    background-color: inherit;
    font-weight: 600;
    color: rebeccapurple;
    border: 0.1px solid rebeccapurple;
}

.btn-toggle:hover {
    opacity: 0.5;
}

.btn-submit {
    padding: 5px 10px;
    border-radius: 10px;
    font-weight: 500;
    color: #fff;
    background-color: rebeccapurple;
}

#content {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
    justify-items: center;
    padding: 30px;
    overflow-x: hidden;
}

.main-weather-display-container {
    display: flex; 
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.main-weather-display {
    display: flex; 
    flex-direction: column;
    align-items: center;
    padding: 10px;
    border: 0.1px solid lightgray;
    border-radius: 5px;
}

.main-weather-display .data-display {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
}

.main-weather-display .temp-display {
    font-size: 1.5rem;
    font-weight: 600;
}

.main-weather-display .day-weather-desc {
    font-size: 1.25rem;
    font-weight: 500;
}

.lower-display {
    padding: 20px;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 10px;
    column-gap: 10x;
    overflow-x: hidden;
}

.weather-card-container {
    display: flex; 
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.weather-card {
    display: flex; 
    flex-direction: column;
    align-items: center;
    padding: 10px;
    border: 0.1px solid lightgray;
    border-radius: 5px;
    width: 100%;
}

.temp-scorch {
    color: crimson;
}

.temp-hot {
    color: orange;
}

.temp-warm {
    color: yellow;
}

.temp-cool {
    color: aliceblue;
}

.temp-cold {
    color: blue;
}

.temp-freeze {
    color: darkblue;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAEA;;AAEA;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,SAAS;IACT,UAAU;IACV,qBAAqB;AACzB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,kBAAkB;IAClB,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,eAAe;IACf,mBAAmB;AACvB;;AAEA;;;;IAII,yBAAyB;IACzB,kBAAkB;IAClB,kBAAkB;AACtB;;AAEA;;;;IAII,eAAe;AACnB;;AAEA;;IAEI,qBAAqB;AACzB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;IACjB,yBAAyB;IACzB,gBAAgB;IAChB,oBAAoB;IACpB,iCAAiC;AACrC;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,iBAAiB;IACjB,mBAAmB;IACnB,gBAAgB;IAChB,WAAW;IACX,+BAA+B;AACnC;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,uBAAuB;IACvB,mBAAmB;IACnB,qBAAqB;IACrB,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,WAAW;AACf;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,aAAa;IACb,6BAA6B;IAC7B,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,WAAW;AACf;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,WAAW;IACX,aAAa;IACb,qCAAqC;IACrC,SAAS;IACT,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,WAAW;AACf;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,aAAa;IACb,6BAA6B;IAC7B,kBAAkB;IAClB,WAAW;AACf;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,eAAe;AACnB","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');\n\n:root {\n    \n}\n\n* {\n    box-sizing: border-box;\n}\n\nbody {\n    margin: 0;\n    padding: 0;\n    font-family: 'Roboto';\n}\n\nimg {\n    max-width: 100%;\n    max-height: 100%;\n    object-fit: contain;\n}\n\nheader {\n    display: flex;\n    flex-direction: row;\n    padding: 10px 20px;\n    justify-content: space-between;\n    align-items: center;\n}\n\nheader nav {\n    display: flex;\n    flex-direction: row;\n    justify